import { Injectable, Inject } from '@angular/core';
import {
    HttpRequest,
    HttpHandler,
    HttpEvent,
    HttpInterceptor,
    HttpErrorResponse,
} from '@angular/common/http';
import { Observable, throwError } from 'rxjs';
import { TokenService } from '../services/token.service';
import { catchError, switchMap } from 'rxjs/operators';
import { OperatingSystemHelper,
    BridgeServiceInvocationService,
} from '@globile/mobile-services';

@Injectable()
export class TokenInterceptor implements HttpInterceptor {
    private _system = new OperatingSystemHelper(this._window);
    constructor(
        private token: TokenService,
        private native: BridgeServiceInvocationService,
        @Inject('WINDOW') private _window
        ) {}

    intercept(
        request: HttpRequest<any>,
        next: HttpHandler
    ): Observable<HttpEvent<any>> {

        return this.token.getToken()
            .pipe(
                switchMap( res => {
                    console.log('TOKEN: '+ res)
                    if (res && request.url.search('/token') === -1 ) {
                        request = request.clone({
                            setHeaders: {
                                Authorization: `Bearer ${res}`
                            }
                        });
                    }
                    console.log("IVAN: "+this._system.getMobileOperatingSystem())
                    // TODO put the native call (ready)
                    // if ( this._system.getMobileOperatingSystem() === 'unknown') {
                    //     return next.handle(request);
                    // } else {
                    //     return this.native.call(request);
                    // }
                    return next.handle(request);

                })
            , catchError((err: HttpErrorResponse) => {
              if (err.status === 401) {
                // this.router.navigateByUrl('/login');
              }
              return throwError( err );
            })
          );
    }
}
