import { Route } from '@angular/router';
import { ModuleId } from '@globile/mobile-services';
import { FullMenuWrapperComponent } from '../modules/full-menu-wrapper/full-menu-wrapper.component';




export const GLOBALPOSITIONROUTE: Route = {
  path: ModuleId.GLOBALPOSITION,
  loadChildren: () => import('@globile/global-position').then((module) => module.GlobalPositionModule),
  data: {
    footer: { display: true},
    showChatbot: true
  }
};

export const PRODUCTDETAILROUTE: Route = {
  path: ModuleId.PRODUCTDETAIL,
  loadChildren: () => import('@globile/product-detail').then((module) => module.ProductDetailModule),
  data: {
    header: {
      left: { icon: 'sn-SYS020', route: 'back'},
      title: {label: 'Account detail'},
      right: {icon: 'sn-SYS015'}
    },
    showChatbot: true
  }
};

export const CARDDETAILROUTE: Route = {
  path: ModuleId.CARDDETAIL,
  loadChildren: () => import('@globile/card-detail').then((module) => module.CardDetailModule),
  data: {
    header: {
      left: { icon: 'sn-SYS020', route: 'back'},
      title: {label: 'Card detail'},
      right: {icon: 'sn-SYS015'}
    },
    showChatbot: true
  }
};


export const CARDMANAGEMENTROUTE: Route = {
  path: ModuleId.CARDMANAGEMENT,
  loadChildren: () => import('@globile/card-management').then((module) => module.CardManagementModule),
  data: {
    header: {
      left: { icon: 'sn-SYS020', route: 'back'},
      title: {label: 'Card detail'},
      right: {icon: 'sn-SYS015', route:  ModuleId.CARDDETAIL}
    },
    showChatbot: true
  }
};

export const FULLMENU: Route = {
  path: ModuleId.FULLMENU,
  component: FullMenuWrapperComponent,
  data: {
    header: { display: false },
    footer: { display: true },
    showChatbot: false,
    animation: 'MovePageRight'
  }
};

export const BRANCHLOCATORROUTE: Route = {
  path: 'branchlocator',
  // loadChildren: () => import('../modules/branch-locator/branch-locator-wrapper.module')
  loadChildren: () => import('../modules/branch-locator/branch-locator-wrapper.module')
  .then( module => module.BranchLocatorModule),
  data: {
    header: {
      left: { icon: 'sn-SYS020', route: 'back'},
      title: {label: 'Branch Locator'},
    },
    footer: {
      display: false
    },
    showChatbot: false
  }
};

export const LOCAL_ROUTES = [
  GLOBALPOSITIONROUTE,
  PRODUCTDETAILROUTE,
  CARDDETAILROUTE,
  CARDMANAGEMENTROUTE,
  BRANCHLOCATORROUTE,
  FULLMENU
];

export const NATIVE_ROUTES = [];
