import {
  trigger,
  transition,
  style,
  query,
  animateChild,
  group,
  animate,
  useAnimation,
  animation
} from '@angular/animations';

export const togglePageAnimation = animation([
  style({ position: 'relative' }),
      query(':enter, :leave', [
        style({
          position: 'absolute',
          top: 0,
          left: 0,
          width: '100%'
        })
      ]),
      query(':enter', [
        style({ left: '{{start}}'})
      ]),
      query(':leave', animateChild()),
      group([
        query(':leave', [
          animate('200ms ease-out', style({ left: '100%'}))
        ]),
        query(':enter', [
          animate('300ms ease-out', style({ left: '0%'}))
        ])
      ]),
      query(':enter', animateChild())
]);

export const movePageRigthAnimation = animation([
  style({ position: 'relative' }),
  style({ position: 'relative' }),
  query(':enter, :leave', [
    style({
      position: 'absolute',
      top: 0,
      right: 0,
      width: '100%'
    })
  ]),
  query(':enter', [
    style({ right: '-100%'})
  ]),
  query(':leave', animateChild()),
  group([
    query(':leave', [
      animate('300ms ease-out', style({ right: '100%'}))
    ]),
    query(':enter', [
      animate('300ms ease-out', style({ right: '0%'}))
    ])
  ]),
  query(':enter', animateChild()),
]);

export const routesInAnimation =
  trigger('routeAnimations', [
    transition('* <=> MovePageRight', [
      useAnimation(movePageRigthAnimation)
    ]),
    transition('* <=> TogglePageLeft', [
      useAnimation(togglePageAnimation, {params: {
        start: '200%'
      }})
      ,
    ]),
    transition('* <=> TogglePageRight', [
      useAnimation(togglePageAnimation, {params: {
        start: '-100%'
      }})
      ,
    ])
  ]);
