import { Component, OnInit, OnDestroy, Inject } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { Subscription, Subject } from 'rxjs';
import { ActivatedRoute, Router, NavigationEnd, NavigationStart, RouterOutlet } from '@angular/router';

import { filter, takeUntil } from 'rxjs/operators';
import { BridgeActivityService, GlobileNavigationService } from '@globile/mobile-services';

import { InitCollectorService } from './services/init.service';
import { TokenService } from './services/token.service';
import { CountryService, MenuHeaderModel, CountriesEnum } from 'sn-common-lib';
import { FOOTER_MENU } from './consts/footerMenu-const';
import { FooterTabBarElement } from '@globile/footer-tabbar';
import { VA_CHATBOT_CONFIG } from './consts/vaChatbot-const';
import { routesInAnimation } from './animations/routes.animations';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss'],
  animations: [
    routesInAnimation
  ]
})
export class AppComponent implements OnInit, OnDestroy {
  hideLoading = false;
  headerOptions: MenuHeaderModel;
  footerOptions = { menu: FOOTER_MENU, display: true};
  private _destroyed = new Subject<void>();
  private state$: Subscription;
  private stateInit$: Subscription;
  private token$: Subscription;
  public vaChatbotConfig = VA_CHATBOT_CONFIG;
  public showChatbot = false;
  public chatbotContext: object;
  public token: string;

  constructor(
    private route: ActivatedRoute,
    private router: Router,
    private init: InitCollectorService,
    private country: CountryService,
    private translate: TranslateService,
    private activity: BridgeActivityService,
    @Inject('WINDOW') private _window,
    private tokenService: TokenService,
    private globileNavigation: GlobileNavigationService
    ) {
      // currency.setCurrency(this.init.getCountry());
      country.setCountry(CountriesEnum.PT);
      translate.setDefaultLang('en');
      // translate.use(this.init.getLanguage());
      translate.use('en');
  }

  ngOnInit(): void {
    this._subscribeRouteTitle();
    this._subscribeToken();
  }
  ngOnDestroy(): void {
    this._destroyed.next();
    this._destroyed.complete();

    this.state$.unsubscribe();
    this.stateInit$.unsubscribe();
    this.token$.unsubscribe();
  }

  prepareRoute(outlet: RouterOutlet) {
    return outlet && outlet.activatedRouteData && outlet.activatedRouteData['animation'];
  }

  onMenuClick(value: string, id: number) {
    switch (value) {
      case 'back':
        this._window.history.back();
        break;
      case 'menu':
        break;
      default:
        if (Boolean(value)) {
          this.globileNavigation.gotToModule([value]);
        }
    }

    //   this.activity.call({ destination : value });

  }

  footerOnClick(event: FooterTabBarElement) {
    if (Boolean(event.route)) {
      this.globileNavigation.gotToModule([event.route]);
    }
  }
  private _subscribeToken() {
    this.token$ = this.tokenService.getToken()
      .pipe(takeUntil(this._destroyed))
      .subscribe(token => (this.token = token));
  }
  private _subscribeRouteTitle() {
    this.stateInit$ = this.router.events
      .pipe(
        takeUntil(this._destroyed),
        filter(event => event instanceof NavigationStart))
      .subscribe(() => {
        this.hideLoading = false;
      });

    this.state$ = this.router.events
      .pipe(
        takeUntil(this._destroyed),
        filter(event => event instanceof NavigationEnd)
      )
      .subscribe(() => {
        this._processMenu();
        this._window.scrollTo(0, 0);
      });
  }
  private _processMenu() {
    let rte = this.route.snapshot;
    const footer = { menu: FOOTER_MENU, display: true};
    let chatbot = false;

    while (rte.firstChild) {
      if (rte.data) {
        rte = rte.firstChild;
        // if (rte.data.hasOwnProperty('showChatbot') && rte.data.showChatbot.display) {
        if (rte.data.hasOwnProperty('showChatbot')) {
          chatbot = rte.data.showChatbot;
          this.chatbotContext = {page_name: (rte as any)._routerState.url};
        }
        if (rte.data.header) {
          this.headerOptions = rte.data.header;
        }
        if (rte.data.footer) {
          footer.display = rte.data.footer.hasOwnProperty('display') ? rte.data.footer.display : footer.display;
          footer.menu = rte.data.footer.hasOwnProperty('menu') ? rte.data.footer.menu : footer.menu;
        }
      }
    }
    this.showChatbot = chatbot;
    this.footerOptions = footer;
    this.hideLoading = true;

  }


}
