import { Injectable } from '@angular/core';
import { NativeDispatcherService, AnalyticTealiumInitModel, BridgeAnalyticService } from '@globile/mobile-services';
import { Observable, Subject, of } from 'rxjs';
import { InitialValuesModel } from '../models/init.model';
import { first } from 'rxjs/operators';
import { TokenService } from './token.service';
import { CountriesEnum } from 'sn-common-lib';
import { LanguageEnum } from '../enums/languajes.enum';

@Injectable({
    providedIn: 'root',
})
export class InitCollectorService {
    private _initValues: InitialValuesModel;
    constructor(
        private bridge: NativeDispatcherService,
        private token: TokenService,
        private analitic: BridgeAnalyticService
    ) {}
    setInitValues(values: InitialValuesModel) {
        this._initValues = values;
        this.fetchValues();
    }
    getInitValues(): Observable<InitialValuesModel> {
        let _result: Observable<InitialValuesModel>;
        if ( this._initValues) {
            _result = of(this._initValues);
        } else {
            const _init = new Subject<InitialValuesModel>();

            this.bridge.callComponent('', {})
                .then( (res: InitialValuesModel) => {
                    this._initValues = res;
                    this.fetchValues();
                    _init.next(res);
                });
            _result = _init.asObservable();
        }
        return _result.pipe(first());
    }
    getCountry(): CountriesEnum {
        return this._initValues ? this._initValues.country : CountriesEnum.ES;
    }
    getLanguage(): LanguageEnum {
        return this._initValues ? this._initValues.language : LanguageEnum.EN;
    }
    private fetchValues() {
        this.token.setToken(this._initValues.token);
        this.analitic.setInitValues(this._initValues.audit);
    }
}
