import { Injectable } from '@angular/core';
import { Params } from '@angular/router';
import { TokenService } from './token.service';

@Injectable({
  providedIn: 'root',
})
export class UrlParamCollectorService {
  private _initParams: Params = {};
  constructor(
    private token: TokenService
  ) { }

  setQueryParams(queryParams: Params) {
    for (const key in queryParams) {
      this._initParams[key] = queryParams[key];
      }
    this.populateToken();
  }
  getParam(param: string) {
    return this._initParams[param];
  }
  private populateToken() {
    if (this._initParams['token']) {
      this.token.setToken(this._initParams['token']);
    }
  }
}
